/* Navbar CSS Start Here */
.navbar{
  z-index: 1;
  width: 100%;
  box-shadow: 0px 0px 5px 0px black;
  position: fixed;
  background-color: rgb(194, 243, 251);
}

.navbar-brand{
  font-size: x-large;
  font-weight: 800;
}

.color-t{
  color: tomato;
}

.d-btn{
  border: 2px solid tomato;
  padding: 5px 10px 5px 10px;
  border-radius: 30px;
  color: tomato;
  box-shadow: 0px 2px 10px rgb(255, 99, 71);
}

a{
  text-decoration: none;
}

.d-btn:hover{
  box-shadow: 0px 0px 10px 3px rgb(255, 99, 71);

  text-shadow: 2px 1px 3px tomato;
}

.nav-btn{
  color: black;
  margin: 0 10px;
  cursor: pointer;
  position: relative;
  font-size: smalle;
}

.nav-btn::after{
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 3px;
  left: 0px;
  transform: scaleX(0);
  background: tomato ;
  transform-origin: bottom right;
  transition: transform 0.4s ease-out;
}

.nav-btn:hover{
  text-shadow: 2px 1px 3px tomato;
  color: tomato;
}

.nav-link:hover::after{
  transform: scaleX(1);
  transform-origin: bottom left;
}


